@font-face{
    font-family:classic_text;
    src: url("clacon.ttf");
}

.resume{
    line-height: 0px;
    color: rgb(55,255,0);
    font-size: 2em;
    text-emphasis: bold;
}
.name{
    color: rgb(55,255,0);
    font-size: 3em;
    animation-duration: 500ms;
    animation-name: blink;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

@keyframes blink{
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
}
.job{
    text-align: left;
}
.popup{
    color: black;
}
.popup button{
    padding:25px;
    margin:25px;
}